@font-face {
    font-family: trans;
    src: url(trans.ttf);
}
@font-face {
    font-family: bbld;
    src: url(bold.ttf);
}
@font-face {
    font-family: reg;
    src: url(reg.ttf);
}
h1{
    font-family: trans;
}
.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.note{
    font-size:0.8em;
}
input{
    font-family: reg;
}
label{
    font-family: bbld;
}
span>label{
    font-family:reg;
}
.input{
    margin: 10px;
    padding: 10px;
    width: 40vh;
    border-radius: 6px;
    background:black;
    outline:none;
    border:none;
    border-bottom: 2px solid #007f5f;
    color:white;
}
h1,label{
    color:white;
}
.year, .members{
    display: flex;
    flex-direction: column;
}
canvas {
    background-color: black;
    position: absolute;
    z-index: -9999;
}
.evensia{
    margin-top: 150px;
    height: 50px;
}
.submit{
    margin-top:20px;
    width: 40vh;
    padding:8px;
    border-radius: 6px;
    background: #007f5f;
    color:white;
    border:none;
}
.submit:hover{
    background-color: #04d19e;
    box-shadow: 0px 0 20px #02fff2;
    
}
.radiobox{
    accent-color: #04d19e;
}
.center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.qr{
    width: 40%;
}
@media screen and (max-width: 768px){
    .input{
        width: 80%;
    }
    .submit{
        width: 80%;
    }
    .qr{
        width: 70%;
    }
}

